.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.logo{
  display: block;
  margin-left: auto;
  margin-right: auto;
  width:50%;
}

.content{
  background:#5786F9;color:white
}

.symbol{
  background:#ffc107;
  color:black;
  border-radius: 50%;
  padding:.4em
}
.radio{
  background:white;
  color:black;
  padding:.2em;
  border-radius: 20%;
}

.modal-xlg{
  max-width: 1200px;
}